.app{
    height: 100vh;
    
    .sections{
        width: 100%;
        height: calc(100vh - 70px); //chinh section trừ đi viewport
        background-color: lightsalmon;
        position: relative;
        top: 70px;
        scroll-behavior: smooth;
        scroll-snap-type:  y mandatory;
        scrollbar-width: none; //danh cho firefox
        &::-webkit-scrollbar{
            display: none;
        }
        >*{
            width: 100vw;
            height: calc(100vh - 70px);
            scroll-snap-align: start;
        }
    }
}